import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";
import {
  Box,
  Typography,
  useMediaQuery,
  TextField,
  Button,
  Alert,
  Collapse,
  Card,
} from "@mui/material";
import { lightGreen } from "@mui/material/colors";

const Summary = () => {
    const navigate = useNavigate();
    //media
    const isNotMobile = useMediaQuery("(min-width: 1000px)");
    // states
    const [text, settext] = useState("");
    const [summary, setSummary] = useState("");
    const [error, setError] = useState("");
  
    //register ctrl
    const handleSubmit = async (e) => {
      e.preventDefault();
      try {
        const { data } = await axios.post("/api/v1/openai/summary", { text });
        console.log(data); // Check the structure of the response here
        setSummary(data.summary); // Correctly set the summary from the API response
      } catch (err) {
        console.log(err);
        if (err.response && err.response.data.error) {
          setError(err.response.data.error);
        } else if (err.message) {
          setError(err.message);
        }
        setTimeout(() => {
          setError("");
        }, 5000);
      }
    };
    
    return (
      <Box
        width={isNotMobile ? "40%" : "80%"}
        p={"2rem"}
        m={"2rem auto"}
        borderRadius={5}
        sx={{ boxShadow: 5 }}
        backgroundColor={lightGreen}
      >
        <Collapse in={error}>
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        </Collapse>
        <form onSubmit={handleSubmit}>
          <Typography variant="h3">Summarize Text</Typography>
  
          <TextField
            placeholder="add your text"
            type="text"
            multiline={true}
            required
            margin="normal"
            fullWidth
            value={text}
            onChange={(e) => {
              settext(e.target.value);
            }}
          />
  
          <Button
            type="submit"
            fullWidth
            variant="contained"
            size="large"
            sx={{ color: "white", mt: 2 }}
          >
            Submit
          </Button>
          <Typography mt={2}>
            not this tool ? <Link to="/" style={{ textDecoration: 'none', color: '#1976d2' }}>GO BACK</Link>
          </Typography>
        </form>
  
        {summary ? (
          <Card
            sx={{
              mt: 4,
              border: 1,
              boxShadow: 0,
              height: "500px",
              borderRadius: 5,
              borderColor: "natural.medium",
              bgcolor: "background.default",
            }}
          >
            <Typography p={2}>{summary}</Typography>
          </Card>
        ) : (
          <Card
            sx={{
              mt: 4,
              border: 1,
              boxShadow: 0,
              height: "500px",
              borderRadius: 5,
              borderColor: "natural.medium",
              bgcolor: "background.default",
            }}
          >
            <Typography
              variant="h5"
              color="natural.main"
              sx={{
                textAlign: "center",
                verticalAlign: "middel",
                lineHeight: "450px",
              }}
            >
              Summary Will Appear Here
            </Typography>
          </Card>
        )}
      </Box>
    );
}

export default Summary