// export default HomePage;
import React from "react";
import { Box, Typography, Card, Stack } from "@mui/material";
import { useNavigate } from "react-router-dom";
import DescriptionRounded from "@mui/icons-material/DescriptionRounded";
import FormatAlignLeftOutlined from "@mui/icons-material/FormatAlignLeftOutlined";
import ChatRounded from "@mui/icons-material/ChatRounded";

const HomePage = () => {
  const navigate = useNavigate();
  
  return (
    <>
      <Box sx={{ display: "flex", flexDirection: "row", flexWrap: "wrap", gap: 3, padding: 3 }}>
        <Box sx={{ width: { xs: "100%", sm: "48%", md: "23%" }, padding: 2 }}>
          <Typography variant="h4" mb={2} fontWeight="bold" color="primary.main">
            Text Generation
          </Typography>
          <Card
            onClick={() => navigate("/summary")}
            sx={{
              boxShadow: 2,
              borderRadius: 3,
              padding: "1rem",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              transition: "transform 0.3s, box-shadow 0.3s",
              "&:hover": {
                transform: "translateY(-10px)",
                boxShadow: 3,
                borderColor: "primary.main",
                cursor: "pointer",
              },
            }}
          >
            <DescriptionRounded sx={{ fontSize: 80, color: "primary.main", marginBottom: "0.5rem" }} />
            <Stack sx={{ textAlign: "center" }}>
              <Typography variant="h5" fontWeight="bold">
                TEXT SUMMARY
              </Typography>
              <Typography variant="h6" color="text.secondary">
                Summarize long text into short sentences
              </Typography>
            </Stack>
          </Card>
        </Box>
        
        <Box sx={{ width: { xs: "100%", sm: "48%", md: "23%" }, padding: 2 }}>
          <Typography variant="h4" mb={2} fontWeight="bold" color="primary.main">
            Paragraph Generation
          </Typography>
          <Card
            onClick={() => navigate("/paragraph")}
            sx={{
              boxShadow: 2,
              borderRadius: 3,
              padding: "1rem",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              transition: "transform 0.3s, box-shadow 0.3s",
              "&:hover": {
                transform: "translateY(-10px)",
                boxShadow: 3,
                borderColor: "primary.main",
                cursor: "pointer",
              },
            }}
          >
            <FormatAlignLeftOutlined sx={{ fontSize: 80, color: "primary.main", marginBottom: "0.5rem" }} />
            <Stack sx={{ textAlign: "center" }}>
              <Typography variant="h5" fontWeight="bold">
                Paragraph
              </Typography>
              <Typography variant="h6" color="text.secondary">
                Generate Paragraph with words
              </Typography>
            </Stack>
          </Card>
        </Box>

        <Box sx={{ width: { xs: "100%", sm: "48%", md: "23%" }, padding: 2 }}>
          <Typography variant="h4" mb={2} fontWeight="bold" color="primary.main">
            AI ChatBot
          </Typography>
          <Card
            onClick={() => navigate("/chatbot")}
            sx={{
              boxShadow: 2,
              borderRadius: 3,
              padding: "1rem",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              transition: "transform 0.3s, box-shadow 0.3s",
              "&:hover": {
                transform: "translateY(-10px)",
                boxShadow: 3,
                borderColor: "primary.main",
                cursor: "pointer",
              },
            }}
          >
            <ChatRounded sx={{ fontSize: 80, color: "primary.main", marginBottom: "0.5rem" }} />
            <Stack sx={{ textAlign: "center" }}>
              <Typography variant="h5" fontWeight="bold">
                Chatbot
              </Typography>
              <Typography variant="h6" color="text.secondary">
                Chat with AI Chatbot
              </Typography>
            </Stack>
          </Card>
        </Box>

        <Box sx={{ width: { xs: "100%", sm: "48%", md: "23%" }, padding: 2 }}>
          <Typography variant="h4" mb={2} fontWeight="bold" color="primary.main">
            JavaScript Converter
          </Typography>
          <Card
            onClick={() => navigate("/js-converter")}
            sx={{
              boxShadow: 2,
              borderRadius: 3,
              padding: "1rem",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              transition: "transform 0.3s, box-shadow 0.3s",
              "&:hover": {
                transform: "translateY(-10px)",
                boxShadow: 3,
                borderColor: "primary.main",
                cursor: "pointer",
              },
            }}
          >
            <ChatRounded sx={{ fontSize: 80, color: "primary.main", marginBottom: "0.5rem" }} />
            <Stack sx={{ textAlign: "center" }}>
              <Typography variant="h5" fontWeight="bold">
                JS Converter
              </Typography>
              <Typography variant="h6" color="text.secondary">
                Translate English to JavaScript code
              </Typography>
            </Stack>
          </Card>
        </Box>
      </Box>
    </>
  );
};

export default HomePage;
